.mainBox{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    background-image: linear-gradient(blue, white);
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}
.createblogContainer {
    width: 30%;
    min-height: 50%;
    background-color: white;
    padding: 2vmax;
    box-shadow: 0  1px 5px gray;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.createblogContainer > form {
    width: 100%;
}
.createblogContainer > form > div {
margin: 1vmax 0;
}
.createblogContainer > form > div > p{
margin-bottom: 3px;
font: 600 1vmax "Roboto";

}
.createblogContainer > form > div > textarea{
    width: 96%;
    padding: 1vmax;
    height: 15vmax;
}
.createblogContainer > form > div > input{
    width: 100%;
    /* min-height: 3vmax; */
    /* margin: 1vmax 0; */
    padding: 1vmax 5px;
    border: 0.5px solid gray;
}
.Description{
    height: 10vmax !important;
}
.submitBtn{
    background-color: green;
    border: none;
    font: 600 1.2vmax "Roboto";
    color: white;
}

@media screen and (max-width:600px) {
    .createblogContainer {
        width: 90%;
        /* height: 50%;  */
       
    }
    .createblogContainer > form > div > textarea{
        width: 99%;
        padding: 1vmax;
        height: 40vmax;
    }
    .createblogContainer > form > div > p{
        margin-bottom: 3px;
        font: 600 2vmax "Roboto";
        
        }
    .createblogContainer > form > div > input{
        width: 100%;
        height: 6vmax;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        font-size: medium;

    }
    .submitBtn{
        font: 700 2.5vmax "Roboto";
    }
    
}