.mainBlogBox{
    max-width: 100%;
    border-bottom: 1px solid gray;
    padding: 1vmax;
    margin: .2vmax 0;
}
.heading{
    font: 700 1.8vmax "Roboto";
}
.description{
    color: rgb(57, 57, 57);
    font-family:Georgia, 'Times New Roman', Times, serif;
}
.btnBox{
    margin-top: 1vmax;
    display: flex;
    justify-content: flex-end;
}
.btnBox > div {
    border: 1px solid gray;
    padding: 0.5vmax;
    margin: 0 .1vmax;
   

}
.btnBox > div > a{
    text-decoration: none;
    color: black;
    font: 600 1vmax "Roboto";
}
.update:hover{
    background-color: rgb(173, 238, 173);
}
.delete:hover{
    background-color: red;
}
@media screen and (max-width:600px) {
    .heading{
        font: 700 2.7vmax "Roboto";
    }
    .description{
        color: rgb(57, 57, 57);
        font-family:Georgia, 'Times New Roman', Times, serif;
    }
    .btnBox{
        margin-top: 1vmax;
        display: flex;
        justify-content: flex-end;
    }
    .btnBox > div {
        border: 1px solid gray;
        padding: 0.5vmax;
        margin: 0 .1vmax;
       
    
    }
    .btnBox > div > a{
        text-decoration: none;
        color: black;
        font: 600 2vmax "Roboto";
    } 
}