.mainBox{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    background-image: linear-gradient(blue, white);
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}
.loginContainer {
    width: 30%;
    height: 50%;
    background-color: white;
    padding: 2vmax;
    box-shadow: 0  1px 5px gray;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.loginContainer > form {
    width: 100%;
}
.loginContainer > form > div > input{
    width: 100%;
    height: 3vmax;
    margin: 1vmax 0;
    padding-left: 5px;
    border: 0.5px solid gray;
}
.submitBtn{
    background-color: green;
    border: none;
    font: 600 1.2vmax "Roboto";
    color: white;
}

@media screen and (max-width:600px) {
    .loginContainer {
        width: 90%;
        height: 50%;
       
    }
 
    .loginContainer > form > div > input{
        width: 100%;
        height: 6vmax;

    }
    .submitBtn{
        font: 700 2.5vmax "Roboto";
    }
    
}