.mainHome{
    width: 100vw;
    max-width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.homeContainer1{
    width: 50vw;
    height: 5vmax;
    display: flex;
    align-items: center;
    background-color: black;
    padding-left: 1vmax;
}
.homeContainer1 > div > a{
    text-decoration: none;
    padding: 1vmax;
    border: 1px solid gray;
    font: 500 1.2vmax "Roboto";
    color: white;
    margin: 0 0.1vmax;
}
.homeContainer2{
    width: 51vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
@media screen and (max-width:600px) {
  
    
    .homeContainer1{
        width: 100vw;
        height: 8vmax;
        display: flex;
        align-items: center;
        background-color: black;
        padding-left: 1vmax;
    }
    .homeContainer1 > div > a{
        text-decoration: none;
        padding: 1.5vmax;
        border: 1px solid gray;
        font: 500 1.8vmax "Roboto";
        color: white;
        margin: 0 0.2vmax;
    }
    .homeContainer2{
        width: 100vw;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    
}